.box {
  width: 350px;
  height: auto;
  background: #99ffeb5e;
  border-radius: 15px;
  box-shadow: 5px 5px 15px #000;
  position: relative;
  z-index: 9;
  padding:2rem;
  overflow: hidden;
}

.form_control {
  width: 60%;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  border: none;
  outline: none;
  background: #fff;
  color: #000;
  font-weight: 600;
  font-family: 'roboto';
  font-size: 1rem;
  border: 1px solid #0006;
}

.inputData {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
}
.move{
  position: relative;
  animation: shimmy 3s infinite ease-in-out alternate-reverse;
}
@keyframes shimmy{
  0%{
    transform:translate(0);
  }
  100%{
    transform:translate(10px);
  }

}

.wave{
  opacity: 0.3;
  position:absolute;
  top:160%;
  left:50%;
  background:#fff;
  width:50rem;
  height:50rem;
  margin-left: -25rem;;
  margin-top: -20rem;;
  transform-origin: 50% 48%;
  border-radius: 43%;
  animation: drift 3000ms infinite  linear;
}
.wave .three{
  animation: drift 5000ms infinite linear;
  z-index: 5 !important;
  opacity: 0.2!important;
}
.wave .two{
  animation: drift1 7000ms infinite linear;
  z-index: 4 !important;
  opacity: 0.1!important;
}
.wave .one{
  animation: drift2 3000ms infinite linear;
  z-index: 2 !important;
  opacity: 0.1!important;
}
h2.locations {
  text-align: center;
  padding-top: 3rem;
  font-size: 3rem;
  color: #000;
  font-family: roboto;
}

h2.locations i {
  color: #000;
}
@keyframes drift{
  from{
    transform: rotate(0drg);
  }
  to{
    transform:rotate(360deg)
  }
}
h1.temp {
  font-size: 2rem;
  color: #000;
  z-index: 9;
  text-align: center;
  padding-top: 5rem;
  position: relative;
  font-family: 'roboto';
}
h3.tempMin_max {
  font-size: 1rem;
  text-align: center;
  color: #000;
  font-weight: 600;
  font-family: sans-serif;
  padding-top: 1rem;
  position: relative;
  z-index: 9;
}
.error{
  font-size: 2rem;
  font-weight: 600;
  font-family: sans-serif;
  color: #000;
  text-align: center;
  padding-top:3rem;
}